h1 {
    font-size: 50px;
}

.hide {
    display: none
}

.rsvp-form {
    position: relative;
    margin: 50px auto;
    padding: 50px;
    border: 6px double #b8d9a5;
    max-width: 600px;
    
    .form-bkg-img {
        background-image: url('../../images/cala-lilly.png');
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 90%;
        height: auto;
        position: absolute;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center center;
    }

    section,
    button,
    hr {
        position: relative;
        z-index: 2;
    }

    &__firstNameLabel {
        margin-top: 30px;
    }

    &__input {
        max-width: 100%;
        height: 30px;
        margin: 10px 0;
        font-size: 20px;
        padding: 5px;
        font-family: "Montserrat", sans-serif;
    }

    &__label {
        font-size: 30px;
        margin: 0 10px 10px;

        &--question {
            margin-top: 30px;
            display: block;
        }

        &--rsvpQuestion {
            font-size: 36px;
            margin-bottom: 20px;
        }
    }

    &__sectionHeader {
        font-size: 36px;
        font-weight: 300;
        border-bottom: 2px solid;
        width: 100px;
        margin: 0 auto 20px;
    }

    &__radio {
        margin: 30px 0;

        &-input {
            margin: 10px 30px 10px 10px;
            height: 15px;
            width: 15px;
        }

        &-label {
            font-size: 24px;
        }

        &-container {
            display: block;
        }
    }

    &__checkbox {
        display: grid;
        grid-template-columns: 15% 85%;
        grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
        row-gap: 10px;
        text-align: left;
        align-items: center;
        width: 200px;
        margin: 0 auto;
        height: 220px;

        &-label {
            font-size: 24px;
            line-height: 24px;
        }
    }

    &__btn {
        height: 50px;
        width: 200px;
        background-color: #b8d9a5;
        color: #05065a;
        font-family: "Dancing Script", cursive;
        font-size: 30px;
        margin-top: 30px;

        &:hover,
        &:focus {
            cursor: pointer;
        }
    }

    hr {
        margin: 50px 0;
        border: 1px ridge #b8d9a5;
    }

    textarea {
        width: 100%;
        height: 100px;
        font-size: 20px;
        padding: 5px;
        margin: 10px 0;
        font-family: "Montserrat", sans-serif;
    }
}

#Guest2Section {
    display: none;
}

.success-message {
    display: block;
    font-size: 60px;
    margin: 100px;

    @media (max-width: 600px) {
        margin: 100px 0;
    }
}

.error-message {
    display: block;
    font-size: 60px;
    margin: 100px;
    color: red;

    @media (max-width: 600px) {
        margin: 100px 0;
    }
}
